
.CardImage {
  /* This assures that the aspec ration is 1:1 */
  height: 100%;
  padding-top: 100%;
  filter: none;
}

.CardImageContainer {
  width: 100%;
  padding: 0 calc(56.25% / 4);
  background-size: 100% 100%;
  background: #555;
}

.CardImageBackground {
  position: absolute;
  z-index: -1;
  background-image: url("/logo.png");
  width: 100%;
  height: 100%;
  /* filter: blur(5px); */
}

